"use client";

import { useEffect, useRef, useState } from "react";
import { useRouter } from "next/navigation";
import Cookies from "js-cookie";

// Components
import ChatSidebar from '../components/ChatSidebar';
import ChatEmptyState from '../components/ChatEmptyState';
// import ChatSuggestedPrompts from '../components/ChatSuggestedPrompts';
import ChatInput from '../components/ChatInput';
import ChatHeader from '../components/ChatHeader';
import ChatMessageList from '../components/ChatMessageList';
import { saveChatHistory, loadChatHistory } from "@/utils/chatStorage";
import { ChatMessage } from "@/types/ChatTypes";

export default function ChatbotPage() {
  const router = useRouter();
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [input, setInput] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const [authChecked, setAuthChecked] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const chatRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const isLoggedIn =
      sessionStorage.getItem("cyberlegal-auth") ||
      Cookies.get("cyberlegal-auth");

    if (!isLoggedIn) {
      router.push("/login");
    } else {
      setAuthChecked(true);
    }
  }, [router]);

  useEffect(() => {
    if (chatRef.current) {
      chatRef.current.scrollTop = chatRef.current.scrollHeight;
    }
  }, [messages]);

  useEffect(() => {
    // Restore chat from localStorage on mount
    const history = loadChatHistory();
    if (history.length > 0) {
      setMessages(history);
    }
  }, []);
  
  useEffect(() => {
    // Save chat history whenever it changes
    if (messages.length > 0) {
      saveChatHistory(messages);
    }
  }, [messages]);

  const handleSend = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim()) return;

    const userMessage: ChatMessage = { sender: "user", content: input.trim() };
    setMessages((prev) => [...prev, userMessage]);
    setInput("");
    setIsTyping(true);

    try {
      const res = await fetch("http://127.0.0.1:8000/api/rag/query", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ query: userMessage.content }),
      });

      const data = await res.json();
      const botReply: ChatMessage = {
        sender: "bot",
        content: data.response,
      };

      setMessages((prev) => [...prev, botReply]);
    } catch (err) {
      console.error("Backend error:", err);
      setMessages((prev) => [
        ...prev,
        {
          sender: "bot",
          content:
            "â ï¸ Sorry, something went wrong while processing your question.",
        },
      ]);
    } finally {
      setIsTyping(false);
    }
  };

  const sendPreset = (text: string) => {
    setInput(text);
  };

  if (!authChecked) return null;

  return (
    <div className="flex h-screen overflow-hidden bg-zinc-900 text-white">
      {/* Sidebar */}
      <ChatSidebar
        sidebarOpen={sidebarOpen}
        setSidebarOpen={setSidebarOpen}
        onClose={() => setSidebarOpen(false)}
      />

      {/* Main Area */}
      <main className="flex flex-col flex-1">
        <ChatHeader setSidebarOpen={setSidebarOpen} />

        {/* Chat UI */}
        <div ref={chatRef} className="flex-1 overflow-y-auto px-6 py-4">
          <div className="max-w-3xl mx-auto space-y-4">
            {messages.length === 0 && !isTyping ? (
              <ChatEmptyState
                onPresetClick={sendPreset}
                input={input}
                onInputChange={(e) => setInput(e.target.value)}
                onSend={handleSend}
              />
            ) : (
              <>
                {/* {messages.map((msg, i) => (
                  <ChatBubble
                    key={i}
                    sender={msg.sender}
                    content={msg.content}
                  />
                ))} */}
                <ChatMessageList messages={messages} isTyping={isTyping} />
                {/* {isTyping && (
                  <div className="text-sm text-zinc-500 animate-pulse">
                    Cyberlegal.AI is typing...
                  </div>
                )} */}
              </>
            )}
          </div>
        </div>

        {/* Input shown only after messages start */}
        {messages.length > 0 && (
          <ChatInput
            input={input}
            onInputChange={(e) => setInput(e.target.value)}
            onSend={handleSend}
          />
        )}
      </main>
    </div>
  );
}
